.accessories {
  background: $black;
  padding: 68px 0 134px;
  @include adaptive(767) {
    padding: 12px 0 39px;
  }
  &__wrap {
    width: 100%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 96px;
    @include adaptive(767) {
      margin-top: 52px;
    }
  }
  &__item {
    display: flex;
    flex-direction: column;
    width: calc(100% / 3 - 16px);
    margin-bottom: 24px;
    @include adaptive(1024) {
      width: calc(50% - 7px);
      margin-bottom: 46px;
    }
    @include adaptive(413) {
      width: 100%;
    }
    &-image {
      width: 100%;
      margin-bottom: 35px;
      &::before {
        content: '';
        display: block;
        padding-top: 100%;
      }
      img {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
    &-desc {
      display: flex;
      flex-direction: column;
      align-items: flex-start;
      padding: 17px 0 0 50px;
      @include adaptive(767) {
        padding: 0;
      }
    }
    &-name {
      font-weight: 700;
      font-size: 2.2em;
      line-height: 1.2;
      color: $white;
      margin-bottom: 20px;
      text-transform: uppercase;
      @include adaptive(767) {
        font-size: 1.5em;
      }
    }
  }
}
