.hello {
  width: 100vw;
  height: 100vh;
  background: $black no-repeat;
  background-size: cover;
  color: $white;
  &::before {
    content: '';
    display: block;
    width: 100%;
    height: 100%;
    background: url(../images/vignette.png) no-repeat center;
    background-size: cover;
  }
  &__content {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 100px 0 25px;
    @include adaptive(767) {
      padding: 58px 0 25px;
    }
    .container {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      width: 100%;
      height: 100%;
      @include adaptive(767) {
        align-items: flex-start;
      }
    }
  }
  &__header {
    font-size: 3.9em;
    line-height: 1.4;
    font-weight: 900;
    margin-bottom: 50px;
    text-transform: uppercase;
    text-align: center;
    @include adaptive(1328) {
      font-size: 3.2em;
    }
    @include adaptive(767) {
      text-align: left;
      font-size: 2.5em;
      line-height: 1.6;
      margin-bottom: 86px;
    }
    @include adaptive(380) {
      margin-bottom: 20px;
      font-size: 1.9em;
    }
  }
  &__readmore {
    @include adaptive(767) {
      height: 74px;
      border-radius: 37px;
      font-size: 2em;
      min-width: 290px;
    }
    @include adaptive(413) {
      height: 36px;
      border-radius: 18px;
      font-size: 1em;
      min-width: unset;
    }
  }
}
