.header {
  position: absolute;
  width: 100%;
  z-index: 100;
  padding-top: 65px;
  padding-bottom: 15px;
  color: $black;
  line-height: 1.2;
  @include adaptive(767) {
    padding-top: 0;
  }
  &.index {
    color: $white;
    .header__logo {
      filter: invert(0);
    }
    .header-button {
      img {
        filter: invert(0);
      }
    }
    .header__contacts {
      @include adaptive(767) {
        background: $black;
      }
    }
    a {
      color: $white;
    }
    .burger-icon {
      &::before,
      &::after {
        background: $white;
      }
    }
  }
  .container {
    display: flex;
    justify-content: space-between;
    @include adaptive(767) {
      flex-wrap: wrap;
    }
  }
  &__logo {
    flex-shrink: 0;
    filter: invert(1);
    @include adaptive(600) {
      width: calc(40vw - 15px);
      padding-right: 15px;
    }
    a {
      display: block;
    }
    img {
      @include adaptive(768) {
        max-height: 70px;
      }
    }
  }
  &__contacts {
    display: flex;
    align-items: center;
    padding-top: 6px;
    margin-left: auto;
    @include adaptive(767) {
      width: 100vw;
      order: -1;
      justify-content: space-between;
      padding: 16px calc(50vw - 50%);
      margin: 0 calc(50% - 50vw) 39px;
      background: $white;
    }
  }
  &__topbar {
    width: auto;
    padding-top: 6px;
    padding-left: 38px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    @include adaptive(767) {
      padding-left: 0;
    }
  }
  &__address {
    max-width: 225px;
    @include adaptive(380) {
      font-size: 10px;
    }
  }
  &__phone {
    color: $black;
    text-decoration: none;
    font-weight: 700;
    font-size: 1.125em;
    margin-left: 38px;
    @include adaptive(380) {
      font-size: 12px;
      margin-left: 10px;
    }
  }
  &-button {
    background: transparent;
    border: none;
    cursor: pointer;
    padding: 0;
    img {
      filter: invert(1);
    }
  }
  &__search {
    margin-left: 0;
    @include adaptive(768) {
      margin-left: 20px;
    }
    &-popup {
      display: flex;
      justify-content: center;
      align-items: center;
    }
    &.active {
      & + .header__search-popup {
        opacity: 1;
        pointer-events: auto;
        z-index: 100;
        padding: 20px;
      }
    }
  }
  &__cart {
    margin-left: 27px;
  }
  .search__popup {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: fit-content;
    max-width: 100%;
    padding: 123px 0;
    margin: 0 auto;
    @include adaptive(1024) {
      padding: 20px 0;
    }
    button[type="submit"] {
      position: absolute;
      right: 0;
      top: 0;
      height: 100%;
      width: 40px;
      background: transparent;
      border: none;
      padding: 0;
      display: flex;
      align-items: center;
      justify-content: center;
      cursor: pointer;
    }
  }
}
.burger {
  color: inherit;
  font-weight: 900;
  font-size: 1.125em;
  display: flex;
  align-items: center;
  margin-left: 36px;
  z-index: 50;
  &-icon {
    width: 25px;
    height: 12px;
    &::before,
    &::after {
      content: '';
      display: block;
      width: 25px;
      height: 2px;
      background: $black;
      position: absolute;
      transition: .3s;
    }
    &::before {
      top: 0;
      transform-origin: left top;
    }
    &::after {
      bottom: 0;
      transform-origin: left bottom;
    }
  }
  span {
    margin-left: 16px;
    margin-right: 10px;
    @include adaptive(1024) {
      display: none;
    }
  }
  &__popup {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 125px 0;
    @include adaptive(1024) {
      padding: 55px 0 0;
    }
    &-icon {
      transition: .3s;
    }
  }
  &.active {
    + .burger__popup {
      opacity: 1;
      pointer-events: auto;
    }
    .burger-icon {
      &::before,
      &::after {
        background: $white;
      }
      @include adaptive(1024) {
        transform: translateY(-70px);
      }
      &::before {
        transform: rotate(45deg);
        top: -3px;
      }
      &::after {
        transform: rotate(-45deg);
        bottom: -4px;
      }
    }
    span {
      color: $white;
    }
  }
  &__nav {
    display: flex;
    justify-content: space-between;
    width: 100%;
    max-width: 100vw;
    padding: 20px;
    font-weight: 900;
    &-wrap {
      display: flex;
      flex-wrap: wrap;
      width: 100%;
      @include adaptive(1024) {
        width: auto;
        max-width: 100%;
        flex-direction: column;
        flex-wrap: nowrap;
      }
    }
    &-parent {
      width: 25%;
      padding-left: 50px;
      padding-right: 25px;
      @include adaptive(1366) {
        padding-left: 0;
      }
      @include adaptive(1024) {
        width: auto;
        margin-bottom: 50px;
      }
      &:last-child {
        @include adaptive(1024) {
          margin-bottom: 0;
        }
      }
      & > a {
        text-transform: uppercase;
        font-size: 1.65em;
      }
    }
    &-submenu {
      font-size: 1.2em;
      li {
        margin-top: 25px;
      }
    }
    ul {
      list-style: none;
      margin: 0 0 35px;
      padding: 0;
      @include adaptive(1024) {
        margin: 0 auto;
      }
    }

    a {
      color: $white;
      text-decoration: none;
      &:hover {
        color: $orange;
      }
    }
  }
}
