@import "../blocks/pagetitle";
@import "../blocks/breadcrumbs";
@import "../blocks/sidebar";
@import "../blocks/catalog";
@import "../blocks/blog";
@import "../blocks/card";
@import "../blocks/recommend";
@import "../blocks/set";
@import "../blocks/detailed";

.page {
  &-inner {
    padding: 45px 0;
    @include adaptive(768) {
      padding: 20px 0;
    }
    .container {
      display: flex;
      align-items: flex-start;
      @include adaptive(768) {
        flex-direction: column;
      }
    }
    &.detail {
      .container {
        flex-direction: row;
        align-items: flex-start;
        @include adaptive(768) {
          flex-direction: column;
        }
        .preview-image {
          position: sticky;
          top: 30px;
          width: 50%;
          flex-shrink: 0;
          display: flex;
          align-items: flex-start;
          @include adaptive(1024) {
            width: 30%;
          }
          @include adaptive(768) {
            position: relative;
            width: 100%;
            max-height: 300px;
            margin-bottom: 35px;
          }
          & + .content {
            padding-left: 95px;
            @include adaptive(1024) {
              padding-left: 45px;
            }
            @include adaptive(768) {
              padding-left: 0;
              padding-top: 40px;
            }
          }
          img {
            max-width: 100%;
            height: auto;
            width: auto;
            @include adaptive(768) {
              max-height: 300px;
            }
          }
          .video {
            display: block;
            &::after {
              content: '';
              display: block;
              width: calc(100% - 90px);
              height: calc(100% - 90px);
              max-width: 85px;
              max-height: 85px;
              background: url("../images/play.png") no-repeat;
              position: absolute;
              right: 45px;
              bottom: 45px;
              left: auto;
              top: auto;
              background-size: contain;
              @include adaptive(1024) {
                max-width: 50px;
                max-height: 50px;
                right: 30px;
                bottom: 30px;
              }
            }
          }
        }
        .content {
          h2 {
            margin: 40px 0 30px;
            padding: 0;
            &::before {
              content: none;
            }
          }
        }
      }
    }
  }
  &-tabs {
    &__header {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      margin-left: -10px;
      margin-right: -10px;
      margin-bottom: 80px;
      @include adaptive(767) {
        margin-bottom: 35px;
      }
      .readmore {
        text-transform: uppercase;
        margin: 0 10px 20px;
        cursor: pointer;
        &.active {
          background: $black;
          color: $white
        }
      }
    }
    &__wrap {
      padding-bottom: 180px;
      @include adaptive(767) {
        padding-bottom: 35px;
      }
    }
    &__item {
      display: none;
      &.active {
        display: flex;
        flex-wrap: wrap;
        margin-left: -25px;
        margin-right: -25px;
      }
      &-column {
        width: calc(50% - 50px);
        margin: 0 25px;
        @include adaptive(767) {
          width: 100%;
          margin-bottom: 35px;
        }
        h3 {
          margin-bottom: 55px;
        }
        .readmore {
          text-transform: none;
        }
      }
    }
    &__instruction {
      display: flex;
      flex-wrap: wrap;
      margin: 0 -5px 35px;
      &-step {
        width: calc(50% - 10px);
        margin: 0 5px 10px;
        img {
          width: 100%;
          height: auto;
          box-shadow: 0 66px 162px 0 rgba(0, 5, 4, .1);
        }
        @include adaptive(413) {
          width: 100%;
        }
      }
    }

  }
}
