.set {
  &__item {
    text-align: center;
    &-image {
      margin-bottom: 53px;
      &::before {
        content: '';
        display: block;
        padding-top: 100%;
      }
      img {
        position: absolute;
        width: 100%;
        height: 100%;
        object-fit: contain;
        padding: 10px;
        top: 0;
        left: 0;
        display: block;
      }
    }
    &-name {
      font-size: 1.4em;
      font-weight: 900;
      margin-bottom: 11px;
    }
    &-art {
      color: $gray;
      font-style: italic;
    }
  }
  &__control {
    height: 90px;
    width: 90px;
    top: 57%;
    @include adaptive(1024) {
      height: 50px;
      width: 50px;
    }
    &.swiper-button-prev {
      left: -15px;
      @include adaptive(1366) {
        left: 15px;
      }
    }
    &.swiper-button-next {
      right: -15px;
      @include adaptive(1366) {
        right: 15px;
      }
    }
    &::after {
      font-size: 28px;
      @include adaptive(1024) {
        font-size: 20px;
      }
    }
  }
}
