.about {
  background: $black;
  color: $graywhite;
  padding: 108px 0 0;
  overflow: hidden;
  @include adaptive(768) {
    padding: 64px 0 0;
  }
  &__info {
    display: flex;
    @include adaptive(768) {
      flex-wrap: wrap;
    }
    &-desc {
      width: 50%;
      z-index: 1;
      padding-right: 35px;
      @include adaptive(768) {
        width: 100%;
        padding: 0 18px 0;
      }
      @include adaptive(413) {
        padding: 0;
      }
      h2 {
        margin-bottom: 50px;
        @include adaptive(768) {
          margin-bottom: 27px;
          text-align: center;
        }
      }
    }
    &-image {
      width: 50%;
      @include adaptive(768) {
        width: 100%;
      }
      img {
        position: absolute;
        right: -80px;
        height: 100%;
        width: auto;
        max-width: unset;
        @include adaptive(768) {
          position: relative;
          right: 0;
          height: auto;
          width: 100%;
        }
      }
    }
  }
  &__priority {
    display: flex;
    justify-content: space-between;
    padding: 45px 0 86px;
    @include adaptive(767) {
      flex-direction: column;
      padding: 29px 18px 86px;
    }
    @include adaptive(413) {
      padding: 29px 0 86px;
    }
    &-item {
      width: calc(100% / 3 - 30px);
      padding-right: 10px;
      @include adaptive(1024) {
        padding-right: 0;
      }
      @include adaptive(767) {
        width: 100%;
        margin-bottom: 13px;
      }
      .subheader {
        color: $orange;
        text-transform: uppercase;
        font-weight: 900;
        font-size: 1.75em;
        @include adaptive(767) {
          margin-bottom: 23px;
        }
      }
    }
  }
  p {
    @include adaptive(767) {
      font-size: 11px;
      margin-bottom: 18px;
    }
  }
}
