.contacts {
  padding: 60px 0 70px;
  @include adaptive(767) {
    padding: 70px 0 82px;
  }&.inner {
       padding-top: 0;
     }
  &__header {
    @include adaptive(767) {
      font-size: 4.2em;
      margin-bottom: 32px;
    }
    @include adaptive(380) {
      font-size: 3.5em;
    }
  }
  &__wrap {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    @include adaptive(1024) {
      flex-wrap: wrap;
    }
    @include adaptive(767) {
      flex-direction: column;
    }
  }
  &__item {
    display: flex;
    flex-direction: column;
    width: calc((100% - 66px) / 4);
    min-height: 32px;
    font-weight: 900;
    font-size: 1.1em;
    line-height: 1.2;
    color: $orange;
    @include adaptive(1024) {
      margin-bottom: 20px;
      width: calc(50% - 11px);
    }
    @include adaptive(767) {
      width: 100%;
      font-size: 1.5em;
      margin-bottom: 32px;
    }
    a {
      color: $orange;
      text-decoration: none;
      cursor: pointer;
      &:hover {
        color: $black;
      }
    }
  }
}
#map {
  width: 100%;
  height: 365px;
  @include adaptive(767) {
    height: 445px;
  }
  @include adaptive(380) {
    height: 250px;
  }
}
