.pagetitle {
  padding: 150px 0 70px;
  color: $black;
  @include adaptive(767) {
    padding: 135px 0 50px;
  }
  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    .container {
      display: flex;
      align-items: center;
      height: 100%;
    }
    span {
      font-size: 12.5em;
      font-weight: 700;
      white-space: nowrap;
      padding-left: 25px;
      opacity: .05;
      @include adaptive(768) {
        font-size: 10em;
      }
      @include adaptive(767) {
        font-size: 8em;
      }
    }
  }
  h1 {
    padding-top: 45px;
    font-size: 3.05em;
    @include adaptive(768) {
      padding-top: 0;
    }
    @include adaptive(767) {
      margin: 0;
      font-size: 3em;
    }
  }
}
