.detailed {
  h2 {
    margin-bottom: 0;
  }
  &__wrap {
    display: flex;
    width: 100%;
    @include adaptive(767) {
      flex-direction: column-reverse;
    }
    &-list {
      width: 27%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      margin-top: 6.4%;
      margin-bottom: 19.1%;
      @include adaptive(767) {
        width: 100%;
        margin: 0;
        height: 2.5em;
      }
    }
    &-item {
      @include adaptive(767) {
        position: absolute;
        left: 0;
        top: 0;
        opacity: 0;
        pointer-events: none;
      }
      &.active {
        opacity: 1;
      }
    }
    &-image {
      width: 73%;
      @include adaptive(767) {
        width: 100%;
      }
      &::before {
        content: '';
        display: block;
        padding-top: 78%;
        @include adaptive(767) {
          content: none;
        }
      }
      img {
        position: absolute;
        top: 0;
        right: 0;
        height: 100%;
        display: block;
        @include adaptive(767) {
          width: 100%;
          height: auto;
          position: relative;
        }
      }
    }
    &-lines {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      margin-top: 8.7%;
      height: 55.6%;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      flex-shrink: 0;
      @include adaptive(767) {
        display: none;
      }
      span {
        display: block;
        width: 100%;
        height: 1px;
        background: $black;
        transform-origin: left;
      }
    }
    &-dots {
      display: none;
      @include adaptive(767) {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        display: block;
      }
      span {
        display: block;
        position: absolute;
        width: 16px;
        height: 16px;
        border-radius: 50%;
        background: $black;
        border: 3px solid $orange;
        &:hover,
        &.active {
          transform: scale(1.3);
        }
      }
    }
  }
}
