.feedback {
  padding: 123px 0;
  display: flex;
  align-items: center;
  @include adaptive(1024) {
    padding: 55px 0;
  }
  &.active {
    opacity: 1;
    pointer-events: auto;
  }
  &__form {
    padding: 0 110px;
    @include adaptive(1024) {
      padding: 0 50px;
    }
    @include adaptive(767) {
      padding: 0;
    }
  }
}
