.card {
  padding-bottom: 0;
  .pagetitle__bg {
    .container {
      align-items: flex-start;
      padding-top: 100px;
    }
    & + .container {
      padding-top: 83px;
      display: flex;
      flex-wrap: wrap;
    }
  }
  .preview-image {
    flex-shrink: 0;
    width: 50%;
    margin-bottom: 88px;
    @include adaptive(767) {
      width: 100%;
      margin-bottom: 55px;
    }
    .video {
      display: flex;
      width: 100%;
      margin-bottom: 33px;
      &::after {
        content: '';
        display: block;
        width: calc(100% - 90px);
        height: calc(100% - 90px);
        max-width: 85px;
        max-height: 85px;
        background: url("../images/play.png") no-repeat;
        position: absolute;
        right: 45px;
        bottom: 45px;
        left: auto;
        top: auto;
        background-size: contain;
        @include adaptive(1024) {
          max-width: 50px;
          max-height: 50px;
          right: 30px;
          bottom: 30px;
        }
      }
      img {
        object-fit: cover;
      }
    }
    img {
      width: 100%;
      object-fit: contain;
    }
  }
  &__info {
    padding-left: 55px;
    width: 50%;
    margin-bottom: 88px;
    @include adaptive(767) {
      width: 100%;
      padding-left: 0;
      margin-bottom: 55px;
    }
    h1 {
      font-size: 3.15em;
      padding: 0;
      margin: 0 0 13px;
    }
    &-article {
      font-style: italic;
      color: $gray;
      font-size: 1.2em;
      margin-bottom: 40px;
    }
    &-desc {
      font-size: 1.2em;
      line-height: 1.75;
      margin-bottom: 35px;
    }
    &-params {
      display: flex;
      margin-bottom: 32px;
      & > div {
        width: 50%;
      }
      .price {
        font-weight: 900;
        font-size: 2.25em;
        margin-right: 10px;
        span {
          font-size: 0.65em;
        }
      }
      .old-price {
        color: $gray;
        font-weight: 900;
        font-size: 1.3em;
        text-decoration: line-through;
        span {
          font-size: 0.65em;
        }
      }
    }
    &-subname {
      color: $gray;
      font-size: 1.1em;
      margin-bottom: 6px;
    }

  }
  &__sizes {
    margin-bottom: 55px;
    width: 50%;
    @include adaptive(1023) {
      width: 100%;
    }
    & + .card__recommend {
      width: 50%;
      @include adaptive(1023) {
        width: 100%;
      }
      & > div {
        padding: 0 45px;
      }
    }
  }
  &__recommend {
    width: 100%;
    margin-bottom: 55px;
    align-self: flex-start;
  }
  h3 {
    font-size: 2.1em;
  }
  .readmore {
    height: 74px;
    border-radius: 37px;
    padding: 0 49px;
    min-width: unset;
    @include adaptive(1024) {
      height: 36px;
      border-radius: 18px;
    }
  }
  & ~ .contacts {
    padding-top: 12px;
  }
  & ~ .gallery {
    padding-top: 83px;
    .gallery__grid-item {
      &:nth-child(4n + 1),
      &:nth-child(4n) {
        width: 41.4%;
        margin-right: 23px;
      }
      &:nth-child(4n + 2),
      &:nth-child(4n + 3) {
        width: calc(58.6% - 46px);
        margin-right: 23px;
      }
    }
    &::after {
      content: none;
    }
  }
}
