.sidebar {
  width: 25%;
  flex-shrink: 0;
  position: sticky;
  top: 30px;
  @include adaptive(768) {
    width: 100%;
    position: relative;
    top: 0;
  }
  ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
    @include adaptive(768) {
      max-height: 0;
      overflow: hidden;
      transition: all 1s;
    }
  }
  li {
    margin-bottom: 25px;
    @include adaptive(768) {
      text-align: center;
    }
  }
  a {
    color: $black;
    text-decoration: none;
    font-size: 1.25em;
    font-weight: 900;
    &:hover {
      color: $orange;
    }
  }
  .active a {
    color: $orange;
  }
  &__toggle {
    display: none;
    color: $orange;
    font-weight: 900;
    font-size: 1.5em;
    border: none;
    margin: 0 auto 20px;
    @include adaptive(768) {
      display: block;
    }
    &::before {
      content: '';
      position: absolute;
      right: -40px;
      top: calc(50% - 3px);
      width: 0;
      height: 0;
      border: 6px solid transparent;
      border-top-color: $orange;
    }
    &.active {
      &::before {
        border-bottom-color: $orange;
        border-top-color: transparent;
        top: calc(50% - 9px);
      }
    }
  }
  & + div {
    width: 75%;
    @include adaptive(768) {
      width: 100%;
    }
  }
}
