body,
html {
  margin: 0;
  padding: 0;
  min-height: 100%;
}

img {
  max-width: 100%;
}

*,
*:after,
*:before {
  box-sizing: border-box;
  outline: none;
  -webkit-font-smoothing: antialiased;
}

* {
  position: relative;
}

body {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  font-family: 'Montserrat', sans-serif;
  font-weight: 400;
  font-size: 16px;
  overflow-x: hidden;
  min-width: 320px;
  min-height: 100vh;
  color: #000;
  @include adaptive(1024) {
    font-size: 12px;
  }
}

::-webkit-scrollbar {
  -webkit-appearance: none;
}

::-webkit-scrollbar:vertical {
  width: 12px;
  @include adaptive(1024) {
    width: 4px;
  }
  @include adaptive(767) {
    width: 2px;
  }
}

::-webkit-scrollbar:horizontal {
  height: 8px;
  @include adaptive(1024) {
    width: 2px;
  }
}

::-webkit-scrollbar-thumb {
  background-color: rgba(0, 0, 0, .85);
  border-radius: 0;
  border: 0;
  transition: all .2s ease-in-out;
}

::-webkit-scrollbar-track {
  background-color: transparent;
  transition: all .2s ease-in-out;
}

a,
label {
  -webkit-tap-highlight-color: transparent;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  margin: -1px;
  position: absolute;
  clip: rect(0 0 0 0);
}

.container {
  @extend %container;
  max-width: 1328px;
  @include adaptive(1310) {
    max-width: 100%;
  }
  @include adaptive(767) {
    max-width: 450px;
  }
  &.mobile-fluid {
    @include adaptive(767) {
      padding-left: 0;
      padding-right: 0;
      max-width: 100%;
    }
  }
}
.container-fluid {
  width: 100%;
  padding: 0;
}

header,
main,
footer {
  flex-shrink: 0;
}

main {
}
@media (max-height: 900px) {
  main {
    min-height: 860px;
  }
}

footer {
  margin-top: auto;
}

.fancybox-navigation {
  position: static;
}
a {
  transition: .3s;
  color: $black;
  &:hover {
    color: $orange;
  }
  &.document {
    text-decoration: none;
    margin-top: 35px;
    padding: 0 20px 0 45px;
    font-size: 1.4em;
    line-height: 1.6;
    @include adaptive(1328) {
      font-size: 1.1em;
    }
    @include adaptive(767) {
      padding-right: 0;
    }
    &::before {
      content: "";
      background: url("../images/doc.png") no-repeat;
      background-size: contain;
      display: block;
      width: 32px;
      height: 1.6em;
      position: absolute;
      left: 0;
      top: 0;
    }
  }
  &.video {
    display: inline-flex;
    &::after {
      content: '';
      display: flex;
      width: 100%;
      height: 100%;
      background: $gray url("../images/play.png") no-repeat 50% 50%;
      position: absolute;
      top: 0;
      left: 0;
      background-size: 85px;
      @include adaptive(1024) {
        background-size: 50px;
      }
    }
  }
  &.additional {
    font-weight: 900;
    font-size: 1.1em;
    margin-right: 35px;
    padding-bottom: 10px;
    text-decoration: none;
    border-bottom: 2px dashed #a11616;
    display: inline-block;
    margin-bottom: 20px;
  }
}
h2 {
  margin: 40px 0 30px;
  text-transform: uppercase;
  font-size: 3.2em;
  line-height: 1.35;
  font-weight: 900;
  @include adaptive(1328) {
    font-size: 2.75em;
    margin: 30px 0 20px;
  }
  @include adaptive(767) {
    font-size: 2.5em;
    line-height: 1.4;
  }
  @include adaptive(413) {
    font-size: 2em;
  }
  &.center {
    text-align: center;
    max-width: 70%;
    margin-left: auto;
    margin-right: auto;
    @include adaptive(767) {
      max-width: unset;
    }
  }
  &.white {
    color: $white;
  }
}
h3 {
  font-size: 1.25em;
  font-weight: 900;
  &:first-child {
    margin-top: 0;
  }
}
p {
  font-size: 1em;
  line-height: 1.7;
  margin-bottom: 30px;
  @include adaptive(768) {
    margin-bottom: 20px;
  }
  @include adaptive(600) {
    font-size: 1em;
  }
}
.attention {
  color: $orange;
  font-weight: 900;
  font-size: 1.5em;
}
.info {
  color: $gray;
}
.content {
  ul {
    list-style-type: none;
    font-size: 1.15em;
    li {
      &::before {
        content: '';
        width: .5em;
        height: .5em;
        background: $orange;
        border-radius: 50%;
        display: block;
        position: absolute;
        left: -1.5em;
        top: .675em;
      }
    }
  }
  ol {
    font-size: 1.15em;
  }
  table {
    width: 100%;
    max-width: calc(100vw - 30px);
    display: flex;
    overflow-x: auto;
    overflow-y: hidden;
    font-size: 1.15em;
    tbody {
      display: table;
      border-collapse: collapse;
      width: 100%;
    }
    th,
    td {
      border: 1px solid $black;
      padding: 5px;
    }
    th {
      background: $orange;
      color: $black;
    }
    tr {
      background: $gray;
      &:nth-child(2n) {
        background: $white;
      }
    }
  }
  .columns-2 {
    display: flex;
    justify-content: space-between;
    margin-bottom: 115px;
    @include adaptive(767) {
      flex-direction: column;
      margin-bottom: 50px;
    }
    & > div {
      width: calc(50% - 35px);
      @include adaptive(767) {
        width: 100%;
      }
    }
  }
  .columns-3 {
    display: flex;
    justify-content: space-between;
    margin-bottom: 50px;
    @include adaptive(767) {
      flex-direction: column;
    }
    & > div {
      width: calc((100% - 140px) / 3);
      @include adaptive(767) {
        width: 100%;
      }
    }
  }
  & > *:last-child {
    margin-bottom: 70px;
  }
}
input {
  font-size: 1em;
  height: 40px;
  width: 430px;
  max-width: 100%;
  border: none;
  border-bottom: $white 3px solid;
  background: transparent;
  color: $white;
  padding-right: 40px;
  @include placeholder {
    color: $white;
    opacity: .3;
  }
  &::-ms-clear {
    width: 0;
    height: 0;
  }
  &::-webkit-search-cancel-button {
    display: none;
  }
}
.readmore {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 60px;
  width: fit-content;
  min-width: 230px;
  max-width: 100%;
  border: 2px solid $black;
  padding: 0 39px;
  border-radius: 30px;
  color: $black;
  font-weight: 900;
  text-decoration: none;
  text-transform: lowercase;
  font-size: 1.2em;
  transition: all .3s;
  background: transparent;
  &:hover {
    background: $black;
    color: $white;
  }
  @include adaptive(1024) {
    height: 36px;
    border-radius: 18px;
    font-size: 1em;
    width: fit-content;
    min-width: unset;
  }
  &.white {
    color: $white;
    border-color: $white;
    border-width: 2px;
    &:hover {
      background: $white;
      color: $black;
    }
  }
  &.label {
    min-width: unset;
    height: 46px;
    border-radius: 24px;
    font-size: 13px;
    padding: 0 24px;
    pointer-events: none;
    margin-bottom: 16px;
  }
}
.swiper-button-next,
.swiper-button-prev {
  color: $white;
  height: 50px;
  width: 50px;
  transition: .3s;
  background: $buttons;
  border-radius: 50%;
  &::after {
    font-size: 20px;
  }
  &:active {
    color: $black;
  }
  &.swiper-button-disabled {
    opacity: 0;
  }
}
.swiper-button-prev {
  left: -35px;
}
.swiper-button-next {
  right: -35px;
}
.swiper-container-horizontal {
  & > .swiper-pagination-bullets {
    bottom: 43px;
    & .swiper-pagination-bullet {
      margin: 0 8px;
      @include adaptive(413) {
        margin: 0 4px;
      }
    }
  }
}
.swiper-pagination {
  &-bullet {
    background: $orange;
    width: 24px;
    height: 24px;
    opacity: .7;
    @include adaptive(413) {
      width: 16px;
      height: 16px;
    }
    &-active {
      opacity: 1;
    }
  }
}
.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: $overlay;
  opacity: 0;
  pointer-events: none;
  transition: .3s;
  &__header {
    color: $white;
    font-weight: 900;
    text-transform: uppercase;
    font-size: 2.1em;
  }
  &__wrap {
    display: flex;
    flex-direction: column;
    margin-top: 65px;
    @include adaptive(767) {
      margin-top: 0;
    }
  }
  p {
    font-weight: 900;
    color: $orange;
    font-size: 1.25em;
  }
  input {
    height: 50px;
    margin-bottom: 40px;
    @include adaptive(767) {
      height: 40px;
      margin-bottom: 10px;
    }
  }
  button[type="submit"] {
    margin-top: 25px;
  }
  .container {
    max-height: 100%;
    overflow: auto;
    overflow-x: hidden;
  }
}
.block-flex {
  display: flex;
  flex-wrap: wrap;
  &._3 {
    & > * {
      width: calc(100% / 3);
      @include adaptive(1024) {
        width: 50%;
      }
      @include adaptive(767) {
        width: 100%;
      }
    }
  }
}
