.catalog {
  &-grid {
    display: flex;
    flex-wrap: wrap;
    padding-bottom: 100px;
    &__item {
      background: $white;
      box-shadow: 0 66px 162px 0 rgba(0, 5, 4, .1);
      width: calc((100% / 3) - 22px);
      margin: 0 11px 22px;
      display: flex;
      flex-direction: column;
      @include adaptive(1024) {
        width: calc(50% - 22px);
      }
      @include adaptive(767) {
        width: 100%;
      }
      &-image {
        width: 100%;
        display: block;
        flex-shrink: 0;
        &::before {
          content: '';
          display: block;
          padding-top: 100%;
        }
        img {
          position: absolute;
          left: 0;
          top: 0;
          width: 100%;
          height: 100%;
          padding: 16px;
          object-fit: contain;
          z-index: 1;
        }
      }
      &-info {
        padding: 0 30px 45px;
        text-align: center;
        display: flex;
        flex-direction: column;
        height: 100%;
      }
      &-name {
        color: $orange;
        font-weight: 900;
        font-size: 1.25em;
        margin-bottom: 11px;
      }
      &-article {
        font-size: .8em;
        font-style: italic;
        color: $gray;
        margin-bottom: 24px;
      }
      &-price {
        font-weight: 900;
        font-size: 1.25em;
        margin-bottom: 20px;
        margin-top: auto;
      }
    }
    &:not(.models) .readmore {
      height: 40px;
      font-size: .8em;
      min-width: unset;
      margin: 0 auto;
    }
    &.models {
      .catalog-grid__item {
        width: calc(50% - 22px);
        padding: 75px 51px;
        @include adaptive(767) {
          width: 100%;
          padding: 40px 30px;
        }
        &-name {
          line-height: 1.3;
        }
        &-text {
          line-height: 1.75;
          height: 5.25em;
          overflow: hidden;
          margin-bottom: 20px;
        }
      }
    }
  }
}
