.blog {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  width: 100%;
  &__item {
    width: calc((100% - 44px) / 3);
    margin-bottom: 22px;
    background-size: cover;
    @include adaptive(767) {
      width: calc(50% - 11px);
    }
    @include adaptive(450) {
      width: 100%;
    }
    &::before {
      content: '';
      display: block;
      padding-top: 100%;
    }
    &-inner {
      position: absolute;
      display: flex;
      justify-content: center;
      align-items: center;
      background: $gray;
      padding: 22px;
      text-align: center;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      opacity: 0;
      pointer-events: none;
      transition: opacity .3s;
      a {
        color: $white;
        text-transform: uppercase;
        text-decoration: none;
        font-weight: 900;
        font-size: 1.4em;
        max-width: 100%;
        max-height: 145px;
        overflow: hidden;
        @include adaptive(767) {
          font-size: 1.2em;
          max-height: 125px;
        }
        &:active {
          color: $orange;
        }
      }
    }
    &:hover {
      .blog__item-inner {
        opacity: 1;
        pointer-events: auto;
      }
    }
  }
}
