.colors {
  h2 {
    position: absolute;
    z-index: 1;
    padding-top: 38px;
    @include adaptive(767) {
      width: 100%;
      text-align: center;
      left: 0;
    }
  }
  &__wrap {
    display: flex;
    @include adaptive(767) {
      flex-wrap: wrap;
    }
    &-image {
      width: 50%;
      display: flex;
      @include adaptive(767) {
        width: 100%;
        min-height: 344px;
      }
      picture {
        width: 100%;
      }
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
      }
    }
  }
}
