.five {
  padding: 135px 0 0;
  &__wrap {
    margin-top: 67px;
    display: flex;
    flex-wrap: wrap;
    &-item {
      width: 300px;
      margin-bottom: 85px;
      color: $gray;
      margin-right: calc(50% - 450px);
      @include adaptive(1023) {
        margin-right: 0;
        width: 50%;
        padding-right: calc(50% - 300px);
        margin-bottom: 50px;
      }
      @include adaptive(767) {
        width: 100%;
        padding-right: 0;
      }
      &:nth-child(1) {
        .five__wrap-image::after {
          content: '01';
        }
      }
      &:nth-child(2) {
        .five__wrap-image::after {
          content: '02';
        }
      }
      &:nth-child(3) {
        margin-right: 0;
        .five__wrap-image::after {
          content: '03';
        }
      }
      &:nth-child(4) {
        .five__wrap-image::after {
          content: '04';
        }
      }
      &:nth-child(5) {
        .five__wrap-image::after {
          content: '05';
        }
      }
    }
    &-image {
      height: 100px;
      margin-bottom: 37px;
      img {
        max-height: 100%;
        z-index: 2;
      }
      &::before {
        content: "";
        display: block;
        background: #e4e4e4;
        opacity: .5;
        height: 114px;
        width: 114px;
        position: absolute;
        left: 37px;
        top: 0;
      }
      &:after {
        display: block;
        line-height: 114px;
        font-size: 4.5em;
        font-weight: 900;
        color: $white;
        position: absolute;
        top: 0;
        left: 83px;
      }
    }
    &-name {
      color: $black;
      font-weight: 900;
      font-size: 1.5em;
      line-height: 1.3;
      margin-bottom: 17px;
    }
    &-desc {
      line-height: 1.75;
    }
  }
}
