.footer {
  background: $black;
  padding: 58px 0 50px;
  color: $graywhite;
  font-size: 13px;
  line-height: 1.5;
  @include adaptive(767) {
    padding: 71px 0 75px;
  }
  .container {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    @include adaptive(767) {
      flex-direction: column;
      padding-left: 42px;
      padding-right: 42px;
    }
  }
  &__logo {
    flex-shrink: 0;
    margin-right: 40px;
    @include adaptive(767) {
      margin-right: 0;
      margin-bottom: 35px;
    }
    img {
      width: 150px;
    }
  }
  &__info {
    margin-right: 90px;
    @include adaptive(1024) {
      margin-right: 40px;
    }
    @include adaptive(767) {
      width: 100%;
      margin-right: 0;
      margin-bottom: 50px;
    }
  }
  &__copy {
    flex-shrink: 0;
    @include adaptive(767) {
      width: 100%;
    }
  }
  a {
    color: $white;
    text-decoration: none;
    &:hover {
      color: $orange;
    }
    span {
      color: $orange;
      font-weight: 900;
    }
  }
}
