.breadcrumbs {
  ol {
    display: flex;
    flex-wrap: wrap;
    padding: 0;
    margin: 63px 0 0 50px;
    list-style: none;
    @include adaptive(767) {
      margin: 20px 0 0 0;
    }
  }
  li {
    color: $black;
    padding-right: 35px;
    &:not(:last-of-type) {
      &::after {
        content: '>';
        display: flex;
        justify-content: center;
        width: 35px;
        height: 100%;
        position: absolute;
        right: 0;
        top: 0;
      }
    }
  }
  a {
    color: $black;
    text-decoration: none;
    &:hover {
      color: $orange;
    }
  }
}
