.chief {
  background: $black;
  &__feedback {
    padding: 130px 0 55px;
    display: flex;
    flex-wrap: wrap;
    @include adaptive(767) {
      padding: 0 0 96px;
    }
    &-image {
      width: 50%;
      padding-top: 80px;
      @include adaptive(767) {
        width: 100%;
        padding-top: 20px;
      }
      img {
        width: 100%;
        height: auto;
      }
    }
    &-action {
      width: 50%;
      padding: 19px calc(50vw - 615px) 0 14px;
      color: $white;
      display: flex;
      flex-direction: column;
      justify-content: center;
      @include adaptive(1440) {
        justify-content: flex-start;
      }
      @include adaptive(1328) {
        padding-right: 23px;
      }
      @include adaptive(767) {
        width: 100%;
        padding: 0 23px;
      }
      h2 {
        font-size: 3em;
        margin: 0 0 30px;
        @include adaptive(1328) {
          font-size: 2.5em;
        }
        @include adaptive(767) {
          margin: 28px 0 13px;
        }
        @include adaptive(413) {
          font-size: 2em;
        }
      }
      .attention {
        margin: 12px 0 38px;
        @include adaptive(767) {
          margin: 0 0 29px;
          font-size: 1.25em;
        }
      }
      .readmore {
        @include adaptive(1024) {
          height: 60px;
          border-radius: 30px;
          font-size: 1.75em;
        }
        @include adaptive(413) {
          font-size: 1em;
          height: 36px;
          border-radius: 18px;
        }
      }
    }
  }
  picture {
    img {
      width: 100%;
    }
  }
}
