.recommend {
  &__slider {
    margin-bottom: 21px;
  }
  &__wrap {
    width: 100%;
  }
  &__item {
    display: flex;
    flex-direction: column;
    &-image {
      margin-bottom: 20px;
      &::before {
        content: '';
        display: block;
        padding-top: 100%;
      }
      img {
        position: absolute;
        width: 100%;
        height: 100%;
        object-fit: cover;
        top: 0;
        left: 0;
      }
    }
    &-name {
      font-size: 1.5em;
      font-weight: 900;
      margin-bottom: 8px;
    }
    &-price {
      margin-bottom: 20px;
      font-size: 1.15em;
      color: $gray;
    }
  }
  &__control {
    top: 43%;
    &.swiper-button-next {
      right: -37px;
      @include adaptive(1366) {
        right: 0;
      }
    }
    &.swiper-button-prev {
      left: -37px;
      @include adaptive(1366) {
        left: 0;
      }
    }
  }
}
