.gallery {
  padding: 110px 0 15px;
  @include adaptive(767) {
    padding: 70px 0 15px;
  }
  &__grid {
    z-index: 1;
    padding-top: 55px;
    @include adaptive(767) {
      padding-top: 46px;
    }
    &-wrap {
      display: block;
      @include adaptive(767) {
        flex-wrap: nowrap;
        display: flex;
      }
    }
    &-item {
      float: left;
      width: auto;
      height: 327px;
      margin-bottom: 23px;
      margin-right: 23px;
      @include adaptive(1328) {
        height: calc(25.8vw - 8px);
      }
      &:nth-child(6n + 1),
      &:nth-child(6n) {
        width: 40.5%;
      }
      &:nth-child(6n + 2),
      &:nth-child(6n + 4){
        width: 23.6%;
      }
      &:nth-child(6n + 3),
      &:nth-child(6n + 5){
        width: calc(35.9% - 46px);
      }
      &:nth-child(3n) {
        margin-right: 0;
      }
      &:nth-child(n) {
        @include adaptive(767) {
          height: 725px;
          margin: 0;
        }
        @include adaptive(413) {
          height: 370px;
        }
      }
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: left;
      }
    }
  }
  &__control {
    display: none;
    @include adaptive(787) {
      display: flex;
    }
  }
  &::after {
    content: '';
    display: flex;
    width: 100%;
    height: 200px;
    background: $black;
    position: absolute;
    bottom: 0;
    left: 0;
    @include adaptive(1328) {
      height: calc(12.9vw + 26px);
    }
    @include adaptive(767) {
      height: calc(50vw - 3px);
      max-height: 181px;
    }
  }
  &__pagination {
    display: none;
    @include adaptive(767) {
      display: block;
    }
  }
  &.content {
    padding-top: 83px;
    .gallery__grid-item {
      &:nth-child(4n + 1),
      &:nth-child(4n) {
        width: 41.4%;
        margin-right: 23px;
      }
      &:nth-child(4n + 2),
      &:nth-child(4n + 3) {
        width: calc(58.6% - 46px);
        margin-right: 23px;
      }
    }
    &::after {
      content: none;
    }
  }
}
