.products {
  padding: 85px 0 63px;
  @include adaptive(767) {
    padding: 50px 0 0;
  }
  &__header {
    &.center {
      @include adaptive(767) {
        text-align: left;
      }
    }
  }
  &__tile {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
  &__item {
    width: calc(25% - 25px);
    color: $gray;
    @include adaptive(767) {
      width: calc(50% - 5px);
      margin-bottom: 38px;
    }
    @include adaptive(413) {
      width: 100%;
      text-align: center;
    }
    &-image {
      display: flex;
      align-items: flex-end;
      justify-content: flex-start;
      margin-bottom: 28px;
      @include adaptive(767) {
        margin-bottom: 5px;
      }
      @include adaptive(413) {
        align-items: center;
        justify-content: center;
      }
      img {
        max-height: 100%;
        object-fit: contain;
        @include adaptive(413) {
          max-height: 230px;
        }
      }
    }
    &.wide {
      width: calc(75% - 9px);
      display: flex;
      align-items: center;
      @include adaptive(767) {
        flex-direction: column;
        align-items: flex-start;
        width: 100%;
      }
      .products__item-image {
        width: calc(200% / 3 + 23px);
        @include adaptive(767) {
          width: 100%;
          margin-bottom: 0;
        }
        img {
          transform: scale(1.1);
          transform-origin: top left;
          @include adaptive(1024) {
            transform: scale(1);
            max-height: 350px;
          }
        }
      }
      .products__item-info {
        width: calc(100% / 3 - 23px);
        @include adaptive(767) {
          width: calc(50% - 15px);
        }
        @include adaptive(413) {
          width: 100%;
        }
      }
      &::before {
        @include adaptive(767) {
          content: '';
          display: block;
          width: 553px;
          height: 553px;
          background: $black;
          border: 89px solid $orange;
          border-radius: 50%;
          position: absolute;
          right: -466px;
          bottom: -164px;
          z-index: 1;
        }
        @include adaptive(413) {
          border-width: 38px;
          width: 500px;
          height: 500px;
          right: -466px;
        }
      }
    }
  }
  &__promo {
    padding-top: 103px;
    @include adaptive(767) {
      padding-top: 0;
    }
    &-item {
      display: flex;
      align-items: flex-start;
      color: $graywhite;
      &:nth-child(2n + 1) {
        flex-direction: row;
        margin-bottom: 52px;
        @include adaptive(767) {
          flex-direction: column;
        }
        .products__promo-image {
          margin-left: auto;
          @include adaptive(767) {
            margin-left: 0;
          }
          img {
            @include adaptive(767) {
              max-height: 320px;
            }
          }
        }
        .products__promo-info {
          width: calc(50% + 98px);
          padding-left: 111px;
          padding-right: calc(50vw - 352px);
          margin-left: 20px;
          @include adaptive(767) {
            width: 100%;
            margin: -38px 0 0;
            padding-left: 52px;
            padding-right: calc(100vw - 240px);
          }
        }
      }
      &:nth-child(2n) {
        flex-direction: row-reverse;
        @include adaptive(767) {
          flex-direction: column;
        }
        .products__promo-image {
          margin-right: auto;
        }
        .products__promo-info {
          width: calc(50% - 17px);
          padding-right: 111px;
          padding-left: calc(50vw - 457px);
          margin-right: 6px;
          margin-top: 69px;
          @include adaptive(1024) {
            padding-left: 55px;
            padding-right: 55px;
          }
          @include adaptive(767) {
            width: 100%;
            margin: -65px 0 0;
            padding-right: 55px;
            padding-left: calc(100vw - 219px);
          }
        }
      }
    }
    &-image {
      z-index: 1;
    }
    &-info {
      padding-top: 106px;
      padding-bottom: 115px;
      background-repeat: no-repeat;
      background-size: cover;
      flex-shrink: 0;
      @include adaptive(767) {
        padding-top: 58px;
        padding-bottom: 67px;
      }
    }
  }
  &-name {
    color: $orange;
    font-size: 2em;
    font-weight: 900;
    text-transform: uppercase;
    margin-bottom: 12px;
    @include adaptive(767) {
      font-size: 1.5em;
    }
  }
  &-desc {
    line-height: 1.75;
    margin-bottom: 30px;
    max-width: 300px;
    @include adaptive(767) {
      font-size: 10px;
      margin-bottom: 8px;
    }
  }
  .readmore {
    @include adaptive(767) {
      font-size: 10px;
      width: 130px;
    }
    @include adaptive(413) {
      margin: 0 auto;
      font-size: 1em;
    }
  }
}
